<template>
  <div class="register">
    <div class="main">
      <Speak></Speak>
      <div class="content">
        <el-input placeholder="昵称能不能有点创意" prefix-icon="el-icon-s-custom" v-model.trim="custom.username" maxlength="6"
          clearable></el-input>
        <div class="d-twoBlock"></div>
        <el-input placeholder="我认为这里填手机号比较好" prefix-icon="el-icon-phone" v-model.trim="custom.phone" clearable></el-input>
        <div class="d-twoBlock"></div>
        <div class="verity display-flex">
          <div class="left">
            <el-input :placeholder="tips_message" prefix-icon="el-icon-question" v-model.trim="code_email" clearable></el-input>
          </div>
          <div class="right">
            <el-button :type="verity_type" @click="getVerityCode" :disabled="verity_disabled">{{verity_timer}}</el-button>
          </div>
        </div>
        <div class="d-twoBlock"></div>
        <el-input placeholder="你想填支付密码也行" type="password" show-password prefix-icon="el-icon-s-grid" v-model.trim="custom.password"
          clearable></el-input>
        <div class="d-twoBlock"></div>
        <div class="bottom display-flex">
          <el-button size="small" type="primary" @click="register">注册</el-button>
          <div class="forget display-flex">
            <span @click="goToLogin">有帐号了,还来注册</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Speak from '@/components/experiment/Speak'
  export default {
    name: 'Register',
    components: {
      Speak
    },
    data() {
      return {
        url: '/user',
        custom: {
          phone: '',
          password: '',
          username: '',
        },
        tips_message: '填写邮箱',
        code_email: '',
        verity_timer: '点这！获取验证码',
        verity_type: 'primary',
        verity_disabled: false,
        code: '',
        timer: null
      }
    },
    methods: {
      goToLogin: function() {
        this.$router.push('/login');
      },
      // 获取验证码
      // 开始倒计时，按钮变为不可选
      getVerityCode: function() {
        if (this.$store.state.email_reg.test(this.code_email)) {
          this.$axios.get(this.url + '/emailVerity/' + this.code_email).then(res => {
            if (res.data.code == 200) {
              // 设置倒计时
              // this.verity_timer = "假装倒计时开始";

              this.tips_message = "填写验证码";

              let num = 60;
              this.timer = setInterval(() => {
                num--;
                if (num > 0) {
                  this.verity_timer = num + "秒后重新获取";
                } else {
                  clearInterval(this.timer);
                  this.tips_message = "填写邮箱";
                  this.verity_disabled = false;
                  this.verity_timer = "点这！获取验证码";
                }
              }, 1000)
              // 按钮变为不可选
              this.verity_disabled = true;
              // 清空邮箱
              this.code_email = '';
              // 存储验证码
              this.code = res.data.body;

              // 提示用户收取验证码
              this.$message({
                message: res.data.message,
                type: 'success'
              })
            } else {
              this.$message({
                message: res.data.message,
                type: 'warning'
              })
            }

          })
        } else {
          this.$message({
            message: '邮箱不正确',
            type: 'warning'
          })
        }


      },
      register: function() {
        if ((/^1[3456789]\d{9}$/.test(this.custom.phone)) && this.custom.username.length <= 6 && this.custom.password !=
          "" && this.code == this.code_email && this.code != '') {
          this.$axios.post(this.url + '/register', this.custom)
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.message,
                  type: 'success'
                })

                // 跳转登录界面
                this.$router.push('/login');
              } else {
                this.$message({
                  message: res.data.message,
                  type: 'warning'
                })
              }
            })
        } else {
          this.$message({
            message: "注册的信息好像不要符合要求",
            type: 'warning'
          })
        }
      },
    }
  }
</script>

<style scoped>
  .content {
    width: 60%;
    margin: 0 auto;
  }

  .bottom {
    justify-content: space-between;
  }

  .forget {
    align-self: flex-end;
  }

  .forget span {
    cursor: pointer;
    display: block;
    margin-left: 20px;
  }

  .forget span:nth-child(1):hover {
    color: #409EFF;
  }

  .forget span:nth-child(2):hover {
    color: #E6A23C;
  }

  .verity {
    width: 100%;
    justify-content: space-between;
  }

  .verity .left {
    width: 60%;
  }
</style>



